import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-rating-detail',
  templateUrl: './rating-detail.component.html',
  styleUrls: ['./rating-detail.component.scss']
})
export class RatingDetailComponent implements OnInit {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  @Input('key-label') keyLabel: any;
  @Input('kind-do') kindDo: string = '';
  msgs: any = [];
  btnText: string;
  groupLevel: any;
  levelStatus: any;
  cunstomGroupName: string = "XXX客户群";
  ratingDetail: string = "群评级-查看详情";
  
  constructor() { }
  ngOnInit() {
    // 当前群评星级
    this.groupLevel = [
      { label: '请选择', value: '' },
      { label: '一星', value: '1' },
      { label: '二星', value: '2' },
      { label: '三星', value: '3' },
      { label: '四星', value: '4' },
      { label: '五星', value: '5' },
    ];
    // 当前群评级状态
    this.levelStatus = [{ label: '请选择', value: '' }];
    this.levelStatus.push({ label: '已建立', value: '1' });
    this.levelStatus.push({ label: '申请中', value: '2' });
    this.levelStatus.push({ label: '已评级', value: '3' });
    this.levelStatus.push({ label: '调整中', value: '4' });
    this.levelStatus.push({ label: '已失效', value: '5' });
    if (this.kindDo) {
      switch (this.kindDo) {
        case 'look':
          this.btnText = "关闭"
          break;
        case 'add':
          this.btnText = "增加"
          break;
        case 'modfiy':
          this.btnText = "确定"
          break;
      }
    }
    if (this.inValue) {
    }

  }
  close(){
    
  }
}
