import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service';
import { GroupGradeInfoBean } from './../../bean/custom-group.bean';
import { Message, SelectItem } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import * as _ from 'lodash';
import { tableMessage } from "app/@uisftech/common/constant/config";

@Component({
  selector: 'app-application-history',
  templateUrl: './application-history.component.html',
  styleUrls: ['./application-history.component.scss'],
  providers: [CusOperationHttpService, Commfunc, CommonHttpService]
})
export class ApplicationHistoryComponent implements OnInit {
  tableMsg2 : any = tableMessage;
  cunstomGroupName: string = "XXX客户群";
  applicationHistory: string = "群评级申请历史";
  arr: any;
  cars: any;
  //模态框
  addDisplay: boolean;//修改、查看
  inValue: any;
  info_list: any;
  keyValue: any;
  addUpHeader: any;
  kindDo: string;
  first_count: number = 0;//计数
  daiban_arr: any = [{
    label: '请选择',
    value: ''
  }]; //代办人下拉菜单
  daiban_id: any = ''; //代办人id
  // bean
  groupGradeInfoBean: GroupGradeInfoBean = new GroupGradeInfoBean();
  // 控件
  msgs: Message[] = [];
  groupLevel: SelectItem[] = [];
  levelStatus: SelectItem[] = [];
  groupLevelSel: any = '';
  levelStatusSel: any = '';
  startDate: Date;
  endDate: Date;
  min_date: Date = new Date();
  // 传参
  groupId: any = '';
  groupName: any = '';
  count_arr: any;
  stu_arr: any;

  constructor(
    public httpService: CusOperationHttpService,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public Commfunc: Commfunc,
    public CommonHttpService: CommonHttpService
  ) {

    ///** 状态-已删除 */

    this.count_arr = ["", "一星", "二星", "三星", "四星", "五星"];
    this.stu_arr = ["已删除", "正常", "待提交", "待审核", "申请驳回"];
    this.arr = [
      {
        ch: '群号',
        label: 'groupId'
      },
      {
        ch: '群名称',
        label: 'groupName'
      },
      {
        ch: '经办人',
        label: 'createUserName'
      },
      {
        ch: '经办日期',
        label: 'createDate'
      },
      {
        ch: '失效日期',
        label: 'thruDate'
      },
      {
        ch: '调整前星级',
        label: 'groupFrontLevel'
      },
      {
        ch: '信用星级',
        label: 'groupLevel'
      },
      {
        ch: '评级状态',
        label: 'statusId'
      }
    ];

  }

  ngOnInit() {
    this.preSet();
    this.daibanInfo(1)//代办人
    // this.selectGroupGradeInfo();
  }
  preSet() {
    // 初始化
    this.groupGradeInfoBean.groupId = '';
    this.groupGradeInfoBean.groupName = '';
    // 当前群评星级
    this.groupLevel = [
      { label: '请选择', value: '' },
      { label: '三星', value: '3' },
      { label: '四星', value: '4' },
      { label: '五星', value: '5' },
    ];
    // 当前群评级状态
    this.levelStatus = [{ label: '请选择', value: '' }];
    this.levelStatus.push({ label: '已删除', value: '0' });
    this.levelStatus.push({ label: '正常', value: '1' });
    this.levelStatus.push({ label: '待提交', value: '2' });
    this.levelStatus.push({ label: '待审核', value: '3' });
    this.levelStatus.push({ label: '申请驳回', value: '4' });
    // 链接传参
    if (this.routerInfo.snapshot.params['groupId']) {
      this.groupId = this.routerInfo.snapshot.params['groupId'];//群名称      
    }
    if (this.routerInfo.snapshot.params['groupName']) {
      this.groupName = this.routerInfo.snapshot.params['groupName'];//群名称      
    }
    this.groupGradeInfoBean.groupId = this.groupId;
    this.groupGradeInfoBean.groupName = this.groupName;
  }
  // selectGroupGradeInfo() {
  //   //群评级查询
  //   this.httpService.selectGroupGradeInfo({groupId:this.groupId}).subscribe(data => {
  //     if (data.returnCode.code == 'success') {
  //       this.inValue = data;
  //     }
  //     else {
  //       this.msgs = [];
  //       data.returnCode.code ? data.returnCode.code : '查询失败！';
  //       this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
  //     }
  //   }, err => {
  //     this.msgs = [];
  //     this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
  //   });
  // }
  //点击操作按钮中的查看按钮
  check(data) {
    console.log(data);
    this.inValue = data;
    this.keyValue = this.arr;
    this.addUpHeader = '群评级-查看详情';
    this.addDisplay = true;
    this.kindDo = 'look'
  }
  daibanInfo(i_2) {
    this.queryList(i_2)
  }
  //添加编辑后的结果
  addCall(event) {
    // alert(JSON.stringify(event));
    this.addDisplay = false;
    this.ngOnInit();
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '成功', detail: '保存成功！' })
  }
  queryList(i_2) {
    let params = {
      groupId: this.groupId
    }
    this.groupLevelSel != '' ? params['groupLevel'] = this.groupLevelSel : '';
    this.startDate !== undefined ? params['startDate'] = this.Commfunc.transDateN(this.startDate) : '';
    // this.startDate !== undefined ? params['startDate']='2017-12-12' : '';
    this.endDate !== undefined ? params['endDate'] = this.Commfunc.transDateN(this.endDate) : '';
    // this.endDate !== undefined ? params['endDate']='2017-12-12' : '';
    this.levelStatusSel != '' ? params['statusId'] = this.levelStatusSel : '';
    this.daiban_id != '' ? params['createUserId'] = this.daiban_id : '';
    let successFnc = data => {
      this.cars = data.infoList;
      this.info_list = data.infoList;
      let count = data.infoList.length;
      data.infoList.forEach((item, i) => {
        this.queryTellerById(item, i, count, i_2);
        this.queryPartyGroupListByPartyId(item, i, count, i_2)
         this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '成功', detail: '查询成功！' })
      });

    }
    this.Commfunc.interfaceComFnc(this.httpService.selectGroupGradeHisInfo(params), successFnc, this.msgs)
  }
  queryPartyGroupListByPartyId(item, i, count, i_2){
    let params = {
      partyIdList : [item.createOrgId]
    }
    this.httpService.queryPartyGroupListByPartyId(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.cars[i]['createOrgName'] = data.partyGroupList[0].groupName;
      } else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    })
  }
  //查询人员姓名
  queryTellerById(item, i, count, i_2) {
    let params = {
      tellerId: item.createUserId
    }
    let successFnc = data => {
      this.first_count++;
      let orgName = '';
      if(data.relationshipList){
        data.relationshipList.forEach(item2 => {
          item2.orgId == item.orgId ? orgName = item2.orgName : '';
        });
      }
      this.cars[i]["groupId"] = this.groupId;
      this.cars[i]["groupName"] = this.groupName;
      this.cars[i]["createUserName"] = data.tellerName;
      this.cars[i]["groupFrontLevel"] = item.groupFrontLevel == '' ? this.count_arr[0] : this.count_arr[item.groupFrontLevel];
      this.cars[i]["groupLevel"] = item.groupLevel == '' ? this.count_arr[0] : this.count_arr[item.groupLevel];
      this.cars[i]["statusId"] = item.statusId == '' ? this.stu_arr[0] : this.stu_arr[item.statusId];
      this.cars[i]["orgName"] = orgName;
      this.cars = _.clone(this.cars);
      if (count == this.first_count) {
        let obj = {}
        this.cars.forEach(item_3 => {
          if (!obj.hasOwnProperty(item_3.createUserId) && item_3.createUserId != '') {
            obj[item_3.createUserId] = true;
            this.daiban_arr.push({
              label: item_3.createUserName,
              value: item_3.createUserId
            })
          }
        });
        if (i_2 == 1) {
          this.cars = []
        }
      }
    }
    this.Commfunc.interfaceComFnc(this.CommonHttpService.queryTellerById(params), successFnc, this.msgs)
  }
  //开始时间   选中
  startSelecte() {
    this.min_date = this.startDate;
  }
}
